import { forwardRef, ComponentProps } from "react";
import styled from "styled-components";
import { color, typography } from '../../components/shared/styles';

export interface StylingButtonIcon{
  id: string;
  icon: 'arrow-left' | 'close' | 'help' | 'share' | 'clipboard' | 'tiza' | 'link' | 'email' | 'microphone' | 'camera' | 'share-screen' | 'more' | 'arrow-dropdown';
}

const Icon = styled.span`
  color: ${color.secondary.nile};
  vertical-align: unset;
  font-size: ${typography.size.m1}
`

export const StyledButtonIcon = styled.button`
  width: 35px;
  text-align:center;
  height: 35px;
  border: none;
  border-radius: 100%;
  background : ${color.grayscale.iron};
  cursor: pointer;
`

export const ButtonIcon = forwardRef<unknown, StylingButtonIcon & JSX.IntrinsicElements['button']>((
  {
    id,
    icon,
    ...rest
  },
  ref
)=>{
  return(
    <StyledButtonIcon
    id={id}
    ref={ref as ComponentProps<typeof StyledButtonIcon>['ref']}
    {...rest}
    >
      <Icon className={"icon-"+icon} />
    </StyledButtonIcon>
  )
})