import React, {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
  useState,
} from "react";
import styled from "styled-components";

export interface ScrollbarProps {}
export interface WrapperScrollProps {
  height?: string;
}

const StyledScrollbar = styled.div<ScrollbarProps & { children: ReactElement }>`
  position: relative;
  overflow: hidden;
  height: 100%;
`;

const WrapperScroll = styled.div<WrapperScrollProps>`
  overflow: scroll;
  height: 100%;
  ${(p) => p.height && `max-height: ${p.height}`};
  scrollbar-width: none;
  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  &:hover div {
    opacity: 1;
  }
`;

const Bar = styled.div`
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 10;
  border-radius: 4px;
  opacity: 0;
  background: transparent;
  transition: opacity 120ms ease-out;

  &.is-vertical {
    width: 6px;
    height: 100%;
    top: 2px;
  }
`;

const Thumb = styled.div`
  position: relative;
  display: block;
  width: 100%;
  height: 0;
  cursor: grab;
  border-radius: inherit;
  background-color: #babacc;
  transition: 0.3s background-color easy-out;

  &:hover {
    background-color: #747487;
  }
`;

export const Scrollbar = forwardRef<
  unknown,
  PropsWithChildren<
    ScrollbarProps & WrapperScrollProps & JSX.IntrinsicElements["span"]
  >
>(({ children, height, ...rest }, ref) => {
  const [scrollY, setScrollY] = useState<string>("0%");
  const [thumbHeight, setThumbHeight] = useState<string>("0%");

  return (
    <StyledScrollbar
      {...rest}
      ref={ref as ComponentProps<typeof StyledScrollbar>["ref"]}
    >
      <WrapperScroll
        onScroll={handleScroll}
        onMouseOver={calcHeightThumb}
        height={height}
      >
        {children}
        <Bar className="is-vertical">
          <Thumb
            style={{ transform: `translateY(${scrollY})`, height: thumbHeight }}
          />
        </Bar>
      </WrapperScroll>
    </StyledScrollbar>
  );

  function calcHeightThumb(e: React.UIEvent<HTMLElement>) {
    var thumbHeight =
      (e.currentTarget.offsetHeight * 100) / e.currentTarget.scrollHeight; //100:regla de 3
    if (thumbHeight != 100) setThumbHeight(thumbHeight + "%");
  }

  function handleScroll(e: React.UIEvent<HTMLElement>) {
    var totalHeight = e.currentTarget.offsetHeight + 6; // El número 6 sale del is-vertical>top*3 (para dar espacio. no tocar borde)
    var scrollPosition = e.currentTarget.scrollTop;
    var y = (scrollPosition * 100) / totalHeight; //100:regla de 3
    setScrollY(y + "%");
  }
});
