import React,{
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement
} from "react";
import styled from "styled-components";
import { color, typography } from "../../components/shared/styles";

export interface TableProps {
  orientation?: "vertical" | "horizontal";
  heading?: Array<string>;
}

export interface TRprops {
  underline?: boolean;
}

const StyledTable = styled.table<TableProps & { children: ReactElement }>`
  width: 100%;
  position: relative;
  border-collapse: collapse;
`;

const TH = styled.th`
  text-align: left;
  margin-bottom: 8px;
  padding: 7px 5px;
`;

const THEAD = styled.thead`
  border-bottom: 1px solid ${color.grayscale.silver};
`;

export const TR = styled.tr<TRprops>`
  margin: 0;
  &:hover {
    background-color: ${color.grayscale.gallery};
  }
  ${(p) => p.underline&&`border-bottom: 1px solid ${color.grayscale.gallery};`}
`;

const WrapperMobile = styled.div`
    & *{
      border: none;
    }
`

const InnerWrap = styled.div`
  border-bottom: 1px solid ${color.grayscale.silver};
  display:flex;
  & th:not(:last-child), td:not(:last-child){
    width:100%;
    border-bottom: 1px solid ${color.grayscale.gallery};
  }
  & th{
    padding: 11px 5px;
    margin: 0;
  }
  & tr{
    display: grid;
    &:hover {
      background-color: transparent;
    }
  }
`

export const TD = styled.td`
  padding: 11px 5px;
  margin: 0;
`;

export const TABLE = forwardRef<
  unknown,
  PropsWithChildren<TableProps & JSX.IntrinsicElements["table"]>
>(({ children, heading, orientation, ...rest }, ref) => {
  return (
    <StyledTable
      ref={ref as ComponentProps<typeof StyledTable>["ref"]}
      {...rest}
    >
      <>
        {orientation == "vertical" ? (
          <>
            {children && (
                <WrapperMobile>
                    {React.Children.map(children, child => (
                        // React.cloneElement(child})
                        <InnerWrap>
                        <THEAD>
                          <tr>
                            {heading?.map((item, index) => {
                              return <TH key={index}>{item}</TH>;
                            })}
                          </tr>
                        </THEAD>
                        {child}
                        </InnerWrap>
                    ))}
                </WrapperMobile>
            )}
          </>
        ) : (
          <>
            <THEAD>
              <tr>
                {heading?.map((item, index) => {
                  return <TH key={index}>{item}</TH>;
                })}
              </tr>
            </THEAD>
            {children}
          </>
        )}
      </>
    </StyledTable>
  );
});
