import React, {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
  useState,
} from "react";
import styled, { css } from "styled-components";
import { color, typography, THEMES } from "../../components/shared/styles";

/**
 * Interfaces
 * ---------------------
 */
export interface TabsProps {
  theme?: typeof THEMES[keyof typeof THEMES];
}
export interface LinkTabProps {
  active?: boolean;
}

/**
 * Inner style
 * ------------------
 */
const StyledTabs = styled.div<TabsProps & { children: ReactElement }>``;

const HeaderTab = styled.span`
  display: block;
  border-bottom: 1px solid ${color.grayscale.gallery};
`;

const LinkTab = styled.span<LinkTabProps>`
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  ${(p) =>
    p.active &&
    css`
      border-bottom: 3px solid
        ${(p: TabsProps) => color.primary[p.theme ? p.theme : "default"]};
      & > span {
        color: ${color.secondary.nile};
      }
    `}
`;

/**
 * Export elements and style
 * ---------------------------
 */

export const Tab = styled.div``;

export const Title = styled.h5`
  font-family: "Urbanist", sans-serif;
  font-size: 16px;
  color: ${color.grayscale.silver};
  text-align: center;
  font-weight: ${typography.weight.bold};
`;

export const Content = styled.div`
  margin-top: 20px;
`;

export const Tabs = forwardRef<
  unknown,
  PropsWithChildren<TabsProps & JSX.IntrinsicElements["div"]>
>(({ children, theme = "default", ...rest }, ref) => {
  const [activeTab, setActiveTab] = useState(0);
  const title: Array<object> = [];
  const content: Array<object> = [];

  React.Children.map(children, (tab) => {
    //@ts-ignore
    var tab_props: Array<any> = tab?.props.children;
    tab_props.map((element) => {
      if (element.type.target == "h5") {
        title.push(element);
      }
      if (element.type.target == "div") {
        content.push(element);
      }
    });
  });

  return (
    <StyledTabs
      ref={ref as ComponentProps<typeof StyledTabs>["ref"]}
      {...rest}
      theme={theme}
    >
      <>
        <HeaderTab>
          {title.map((item, index) => (
            <LinkTab
              onClick={() => handleClick(index)}
              key={index}
              theme={theme}
              active={activeTab === index}
            >
              {item}
            </LinkTab>
          ))}
        </HeaderTab>

        {content.map((item, index) => {
          if (activeTab === index) return item;
        })}
      </>
    </StyledTabs>
  );
  function handleClick(index: number) {
    setActiveTab(index);
  }
});
