import {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { color, typography, THEMES } from "../../components/shared/styles";

export interface LinkProps {
  id: string;
  theme?: typeof THEMES[keyof typeof THEMES];
  backButton?: boolean;
}

const StylingLink = styled.span<LinkProps & { children: ReactElement }>`
  text-decoration: none;
  font-size: ${typography.size.m2}px;
  font-weight: 500;
  cursor: pointer;
  vertical-align: middle;

  ${(props) =>
    props.theme == "jungle" &&
    `
    color: ${color.primary.jungle};
  `}

  ${(props) =>
    props.theme == THEMES.HAVELOCK &&
    `
    color: ${color.primary.havelock};
  `}

  ${(props) =>
    props.theme == THEMES.PUNCH &&
    `
    color: ${color.primary.punch};
  `}

  ${(props) =>
    props.theme == THEMES.STUDIO &&
    `
    color: ${color.primary.studio};
  `}

  &:hover>.lbl {
    text-decoration: underline;
  }
  .icon {
    margin-right: 11px;
  }
`;

export const LblLink = forwardRef<
  unknown,
  PropsWithChildren<LinkProps & JSX.IntrinsicElements["a"]>
>(
  (
    { children, id, theme = "jungle", backButton, ...rest },
    ref
    // ref={ref as ComponentProps<typeof StyledBigButton>['ref']}
  ) => {
    return (
      // <StylingLink>
      <>
        <StylingLink
          className="lbl"
          id={id}
          {...rest}
          theme={theme}
          ref={ref as ComponentProps<typeof StylingLink>["ref"]}
        >
          <>
            {backButton && <span className="icon icon-arrow-left"></span>}
            {children}
          </>
        </StylingLink>
      </>
      // </StylingLink>
    );
  }
);
