import { ComponentProps, forwardRef, PropsWithChildren, useRef } from "react";
import styled from "styled-components";
import { color, THEMES, typography } from "../../components/shared/styles";

export interface CheckboxProps {
  label?: string;
  name: string;
  checked?: boolean;
}

export interface CheckStyleProps {
  theme?: typeof THEMES[keyof typeof THEMES];
  disabled?: boolean;
}

export const Checkbox = forwardRef<
  unknown,
  PropsWithChildren<
    CheckboxProps & CheckStyleProps & JSX.IntrinsicElements["input"]
  >
>(({ theme = "default", disabled, name, checked, label, children }, ref) => {
  const inputRef = useRef(ref as ComponentProps<typeof Container>["ref"]);
  return (
    <Container className="container" theme={theme} disabled={disabled} ref={inputRef}>
      {label}
      {children}
      <input type="checkbox" defaultChecked={checked} disabled={disabled} name={name} />
      <span className="checkmark"></span>
    </Container>
  );
});

const Container = styled.label<CheckStyleProps>`
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: ${color.grayscale.shark};
  font-family: ${typography.type.primary};
  font-size: ${typography.size.m1}px;


  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    border-radius: 2px;
    // background-color: #eee;
    border: 1px solid ${color.grayscale.grav};

    &:after {
      content: "";
      position: absolute;
      display: none;
      left: calc(50% - 2px);
      top: calc(50% - 6px);
      width: 3px;
      height: 8px;
      border: solid white;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }

  & input:checked ~ .checkmark {
    background-color: ${(p: CheckStyleProps) => color.primary[p.theme ? p.theme : "default"]};
    border: 1px solid ${(p: CheckStyleProps) => color.primary[p.theme ? p.theme : "default"]};
  }

  & input:checked ~ .checkmark:after {
    display: block;
  }

  // Disabled ...
  ${(p) =>
    p.disabled &&
    `
    color: ${color.grayscale.iron};
    cursor: default;
    & input ~ .checkmark {
      border: 1px solid ${color.grayscale.iron};
    }
    & input:checked ~ .checkmark {
      background-color: ${color.grayscale.iron};
      border: 1px solid ${color.grayscale.iron};
    }
    & .checkmark:after{
      background: ${color.grayscale.iron};
    }
  `}
`;
