import { forwardRef, PropsWithChildren } from "react";
import styled from "styled-components";
import { Div, DivProps, P } from "../..";

export interface GridGeneralProps {
  /** Margin Top */
  mt?: number;
  /** Margin Bottom */
  mb?: number;
  /** Margin left */
  ml?: number;
  /** Margin right */
  mr?: number;
  /** Padding horizontal */
  ph?: number;
  /** Padding vertical */
  pv?: number;
}

export interface RowProps {
  fullWidth?: boolean;
}

export interface ColProps {
  size?: number;
}

export function Row({
  children,
  ...rest
}: RowProps & GridGeneralProps & React.HTMLAttributes<HTMLDivElement>) {
  return <StyledRow {...rest}>{children}</StyledRow>;
}

export const Col: React.FC<
  ColProps & GridGeneralProps & React.HTMLAttributes<HTMLDivElement>
> = ({ children, ...rest }) => {
  return <StyledCol {...rest}>{children}</StyledCol>;
};

export const Hcol = forwardRef<
  unknown,
  PropsWithChildren<GridGeneralProps & DivProps & JSX.IntrinsicElements["div"]>
>(({ children, ...rest }, ref) => {
  return (
    <StyledHcol {...rest} ref={ref}>
      {children}
    </StyledHcol>
  );
});

const StyledRow = styled(Div)<RowProps & GridGeneralProps>`
  position: relative;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-flow: row wrap;
  box-sizing: border-box;

  ${(props) => props.mb && `margin-bottom:${props.mb}px;`}

  ${(props) =>
    props.fullWidth
      ? `padding-right:0;padding-left:0;`
      : `
    @media (min-width: 1064px) {
      padding-right: 40px;
      padding-left: 40px;
    }
    padding-right: 20px; padding-left: 20px;
  `}
`;

const StyledCol = styled(Div)<ColProps & GridGeneralProps>`
  min-width: 0;
  box-sizing: border-box;

  @media (min-width: 1064px) {
  }

  // Padding Horizontal
  // -----
  ${(p) =>
    p.ph &&
    `
    padding-left:${p.ph}px;
    padding-right:${p.ph}px;
  `}

  // Padding Vertical
  // -----
  ${(p) =>
    p.pv &&
    `
    padding-top:${p.pv}px;
    padding-bottom:${p.pv}px;
  `}

  // Margin Vertical
  // -----
  ${(p) =>
    p.mt &&
    `
    margin-top:${p.mt}px;
  `}
  ${(p) =>
    p.mb &&
    `
    margin-bottom:${p.mb}px;
  `}
  // Margin Horizontal
  // -----
  ${(p) =>
    p.ml &&
    `
    margin-left:${p.ml}px;
  `}
  ${(p) =>
    p.mr &&
    `
    margin-right:${p.mr}px;
  `}

  ${(p) =>
    p.size ? `max-width: ${(p.size * 100) / 4}%;` : `max-width: 25%;`}
  ${(p) =>
    p.size ? `flex: 0 0 ${(p.size * 100) / 4}%;` : `flex: 0 0 25%;`}
  @media (max-width: 750px) {
    ${(p) =>
      p.size == 21 || p.size == 31
        ? `max-width: ${(p.size * 100) / 4}%;`
        : `max-width: 100%;`}
    ${(p) =>
      p.size == 21 || p.size == 31
        ? `flex: 0 0 ${(p.size * 100) / 4}%;`
        : `flex: 0 0 100%;`}
  }
`;

const StyledHcol = styled(Div)`
  flex: 0 0 12.5%;
  max-width: 12.5%;
  min-width: 0;
  box-sizing: border-box;
`;
