import { ComponentProps, forwardRef, PropsWithChildren, useRef } from "react";
import styled from "styled-components";
import { color, THEMES, typography } from "../../components/shared/styles";

export interface SwitchProps {
  label?: string;
  name: string;
  checked?: boolean;
}

export interface SwitchStyleProps {
  theme?: typeof THEMES[keyof typeof THEMES];
  disabled?: boolean;
}

export const Switch = forwardRef<
  unknown,
  PropsWithChildren<
    SwitchProps & SwitchStyleProps & JSX.IntrinsicElements["input"]
  >
>(({ theme = "default", label, name, checked, disabled }, ref) => {
  const inputRef = useRef(ref as ComponentProps<typeof StyledSwitch>["ref"]);
  return (
    <Container>
      <StyledSwitch theme={theme} disabled={disabled} ref={inputRef}>
        <input
          type="checkbox"
          name={name}
          defaultChecked={checked}
          disabled={disabled}
        />
        <span className="slider round"></span>
      </StyledSwitch>
      <span className="label">{label}</span>
    </Container>
  );
});

const Container = styled.div`
  display: block;
  margin-bottom: 10px;
  .label {
    font-family: ${typography.type.primary};
    font-size: ${typography.size.m1}px;
    color: ${color.grayscale.black};
  }
`;

const StyledSwitch = styled.label<SwitchStyleProps>`
  position: relative;
  display: inline-block;
  width: 35px;
  height: 18px;
  margin-right: 7px;

  & input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;

    &:before {
      position: absolute;
      content: "";
      height: 16px;
      width: 16px;
      left: 1px;
      bottom: 1px;
      background-color: white;
      -webkit-transition: 0.4s;
      transition: 0.4s;
    }
  }

  input:checked + .slider {
    background-color: ${(p: SwitchStyleProps) =>
      color.primary[p.theme ? p.theme : "default"]};
  }

  input:focus + .slider {
    box-shadow: 0 0 1px
      ${(p: SwitchStyleProps) => color.primary[p.theme ? p.theme : "default"]};
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(17px);
    -ms-transform: translateX(17px);
    transform: translateX(17px);
  }

  .slider.round {
    border-radius: 34px;
    &:before {
      border-radius: 50%;
    }
  }

  // Disabled ...
  ${(props) =>
    props.disabled &&
    `
    color:${color.grayscale.iron};
    cursor: default;
    input:focus + .slider {
      box-shadow: 0 0 1px ${color.grayscale.iron};
    }
    input:checked + .slider {
      background-color: ${color.grayscale.iron};
    }
  `}
`;
