import {
  ComponentProps,
  forwardRef,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { H4 } from "../..";
import { color } from "../../components/shared/styles";

export interface SectionProps {
  heading?: string;
}

const StyledSection = styled.section<SectionProps & { children: ReactElement }>`
  padding-bottom: 60px;
`;

const Heading = styled(H4)`

  position: relative;
  color: ${color.grayscale.shark};
  padding-bottom: 30px;

  &:after{
    content: "";
    position: absolute;
    border-bottom: 1px solid ${color.grayscale.gallery};
    width: 100%;
    left:0;
    top: 30%;
  }
`

const Mask = styled.span`
  background-color: white;
  position: sticky;
  z-index:1;
  padding-right: 12px;
`

export const Section = forwardRef<
  unknown,
  PropsWithChildren<SectionProps & JSX.IntrinsicElements["section"]>
>(({ heading, children, ...rest }, ref) => {
  return (
    <StyledSection
      {...rest}
      ref={ref as ComponentProps<typeof StyledSection>["ref"]}
    >
      <>
      <Heading>
        {heading&&<Mask>{heading}</Mask>}
      </Heading>
        {children}
      </>
    </StyledSection>
  );
});
