import { HTMLAttributes } from "react";
import { Ichildren } from "../..";
import { THEMES } from "../../components/shared/styles";
import {
  StyledBarMenu,
  StyledBar,
  StyledBarItem,
  SidebarTrigger,
  StyledLayout,
  StyledBarFooter,
  StyledContentOut,
} from "./sidebar.styles";


export interface Ilayout {
  theme?: typeof THEMES[keyof typeof THEMES];
  dark?: boolean;
}

export interface Iitem {
  active?: boolean;
  icon?: React.ReactNode;
}

export interface Ibar {
  isOpen?: boolean;
}

export function SidebarLayout({
  children,
  theme = "default",
  ...rest
}: Ichildren & Ilayout) {
  return (
    <StyledLayout theme={theme} {...rest}>
      {children}
    </StyledLayout>
  );
}

export function Bar({ children, isOpen, ...rest }: Ichildren & Ibar) {
  if(isOpen){
    document.documentElement.style.setProperty("--leftSidebarWidth", "220px")
  }else{
    document.documentElement.style.setProperty("--leftSidebarWidth", "55px")
  }
  return (
    <StyledBar id="b" {...rest} isOpen={isOpen}>
      {children}
    </StyledBar>
  );
}

Bar.Menu = function BarMenu({ children, ...rest }: Ichildren) {
  return <StyledBarMenu {...rest}>{children}</StyledBarMenu>;
};

Bar.Item = function BarItem({
  children,
  active,
  icon,
  ...rest
}: Ichildren & Iitem & HTMLAttributes<HTMLLIElement>) {
  return (
    <StyledBarItem
      className={`li-item ${active && "active"}`}
      active={active}
      {...rest}
    >
      {
        icon&&<span className="item-icon">{icon}</span>
      }
      <span className="item-text">{children}</span>
    </StyledBarItem>
  );
};

Bar.FloatArrowTrigger = function BarFloatArrowTrigger({
  children,
  ...rest
}: Ichildren & HTMLAttributes<HTMLButtonElement>) {
  return <SidebarTrigger id="btnOpenSidebar" icon="arrow-left" {...rest} />;
};

Bar.Footer = function BarFooter({ children, ...rest }: Ichildren) {
  return <StyledBarFooter {...rest}>{children}</StyledBarFooter>;
};

export function ContentOut({ children, ...rest }: Ichildren) {
  return <StyledContentOut {...rest}>{children}</StyledContentOut>;
}

