import {
  ComponentProps,
  forwardRef,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { color } from "../../components/shared/styles";

export interface CardProps {
}

const StyledCard = styled.div<CardProps & { children: ReactElement }>`
  border: 1px solid ${color.grayscale.dust};
  border-radius: 3px;
  background-color: white;
  padding: 25px 20px;
  width: fit-content;
`;


export const Card = forwardRef<
  unknown,
  PropsWithChildren<CardProps & JSX.IntrinsicElements["section"]>
>(({ children, ...rest }, ref) => {
  return (
    <StyledCard
      {...rest}
      ref={ref as ComponentProps<typeof StyledCard>["ref"]}
    >
      <>
        {children}
      </>
    </StyledCard>
  );
});
