import React, {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { color, THEMES } from "../../components/shared/styles";

export interface ProgressbarProps {
  theme?: typeof THEMES[keyof typeof THEMES];
}

export interface BarProps {
  progress?: number;
}

const StyledProgressbar = styled.span<
  ProgressbarProps & { children: ReactElement }
>`
  display: block;
  width: 100%;
  height: 6px;
  border-radius: 6px;
  position: relative;
  box-shadow: inset 0 0 2px 0 rgba(0, 0, 0, 0.1);
  overflow: hidden;
  background: ${color.grayscale.gallery};
`;

const StyledBar = styled.span<BarProps>`
  position: absolute;
  background-color: ${(p: ProgressbarProps) => color.primary[p.theme ? p.theme : "default"]};
  width: ${(p: BarProps) => p.progress}%;
  transition: .3s;
  height: 100%;
  box-shadow: inset 0 0 2px 0 rgba(0, 0, 0, 0.1);
  border-radius: 6px;
`;

export const Progressbar = forwardRef<
  unknown,
  PropsWithChildren<ProgressbarProps & BarProps & JSX.IntrinsicElements["span"]>
>(({ children, theme="default", progress = 0, ...rest }, ref) => {
  return (
    <StyledProgressbar
      theme={theme}
      {...rest}
      ref={ref as ComponentProps<typeof StyledProgressbar>["ref"]}
    >
      <>
        <StyledBar theme={theme} progress={progress} />
        {children}
      </>
    </StyledProgressbar>
  );
});
