import { ComponentProps, forwardRef, PropsWithChildren, useRef } from "react";
import styled from "styled-components";
import { color, THEMES, typography } from "../../components/shared/styles";

export interface RadioProps {
  label?: string;
  name: string;
  checked?: boolean;
}

export interface StyleProps {
  theme?: typeof THEMES[keyof typeof THEMES];
  disabled?: boolean;
}

export const Radio = forwardRef<
  unknown,
  PropsWithChildren<RadioProps & StyleProps & JSX.IntrinsicElements["input"]>
>(({ label, name, theme = "default", checked, disabled }, ref) => {
  const inputRef = useRef(ref as ComponentProps<typeof Container>["ref"]);
  return (
    <Container theme={theme} disabled={disabled} ref={inputRef}>
      {label}
      <input type="radio" name={name} defaultChecked={checked} disabled={disabled} />
      <span className="checkmark"></span>
    </Container>
  );
});

const Container = styled.label<StyleProps>`
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: ${typography.type.primary};
  font-size: ${typography.size.m1}px;
  color: ${color.grayscale.black};

  & input {
    position: absolute;
    opacity: 0;
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    border: 1px solid ${color.grayscale.grav};
  }

  & input:checked ~ .checkmark {
    border: 1px solid #979797;
  }

  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  & input:checked ~ .checkmark:after {
    display: block;
  }

  & .checkmark:after {
    top: calc(50% - 5px);
    left: calc(50% - 5px);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: ${(p: StyleProps) => color.primary[p.theme ? p.theme : "default"]};
  }

  // Disabled ...
  ${(props) =>
    props.disabled &&
    `
    color: ${color.grayscale.iron};
    cursor: default;
    & input ~ .checkmark {
      border: 1px solid ${color.grayscale.iron};
    }
    & input:checked ~ .checkmark {
      border: 1px solid ${color.grayscale.iron};
    }
    & .checkmark:after{
      background: ${color.grayscale.iron};
    }
  `}
`;
