import React, {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled, { keyframes } from "styled-components";
import { color, THEMES } from "../../components/shared/styles";

export interface SpinnerProps {
  size?: "small" | "medium" | "large";
  theme?: typeof THEMES[keyof typeof THEMES];
  inline?: boolean;
}

const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

const StyledSpinner = styled.span<SpinnerProps & { children: ReactElement }>`
  display: ${(p) => p.inline && "inline-"}block;
  vertical-align: middle;
  border: 1px solid ${color.secondary.nile}40;
  border-top: 1px solid;
  border-top-color: ${(p: SpinnerProps) => color.primary[p.theme ? p.theme : "default"]};
  border-radius: 50%;
  width: ${(p) =>
    p.size === "large" ? 45 : p.size === "small" ? 20 : 30}px;
  height: ${(p) =>
    p.size === "large" ? 45 : p.size === "small" ? 20 : 30}px;
  animation-name: ${spin};
  animation-duration: 1.6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
`;

export const Spinner = forwardRef<
  unknown,
  PropsWithChildren<SpinnerProps & JSX.IntrinsicElements["span"]>
>(({ children, inline, theme, ...rest }, ref) => {
  return (
    <StyledSpinner
      inline={inline}
      theme={theme}
      {...rest}
      ref={ref as ComponentProps<typeof StyledSpinner>["ref"]}
    >
      <>{children}</>
    </StyledSpinner>
  );
});
