import React, {
  ComponentProps,
  forwardRef,
  PropsWithChildren,
  ReactElement,
  ReactNode,
} from "react";
import styled from "styled-components";
import { darken, opacify } from "polished";
import { color, THEMES, typography } from "../../components/shared/styles";
import { easing } from "../../components/shared/animation";

const SIZES = {
  SMALL: "small",
  MEDIUM: "medium",
  LARGE: "large",
} as const;

export interface StylingProps {
  /**
   * El botón está desactivado?
   */
  disabled?: boolean;
  /**
   * Habilitado si el botón lleva icono.
   * Pendiente de edición...
   */
  containsIcon?: boolean;
  /**
   * Tamaño del botón
   */
  size?: typeof SIZES[keyof typeof SIZES];
  /**
   * Apariencia del botón
   */
  theme?: typeof THEMES[keyof typeof THEMES];
  /**
   * Botón en bordes
   */
  outline?: boolean;
  /**
   * Botón redondeado
   */
  rounded?: boolean;
  /**
   * Ancho completo según su contenedor
   */
  fullWidth?: boolean;
}

export const StyledButton = styled.button<StylingProps>`
  border-width: ${(p) => (p.outline ? "1px" : "0")};
  border-style: solid;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-color: ${(p: StylingProps) =>
    color.primary[p.theme ? p.theme : "default"]};
  background: ${(p: StylingProps) =>
    p.outline ? "transparent" : color.primary[p.theme ? p.theme : "default"]};
  color: ${(p: StylingProps) =>
    !p.outline
      ? color.grayscale.white
      : color.primary[p.theme ? p.theme : "default"]};
  padding: ${(p) =>
    p.size === SIZES.SMALL ? "11px 0.9125em" : "11px 0.9125em"};
  font-size: ${(p) =>
    p.size === SIZES.SMALL ? typography.size.s3 : p.size === SIZES.LARGE? typography.size.m2 : typography.size.m1}px;
  font-weight: ${typography.weight.medium};
  font-family: ${typography.type.primary};
  border-radius: ${p=>p.rounded?"100px": "3px"};
  min-width: 8.0625em;
  ${p=>p.fullWidth&&`width: 100%;`}
  min-height: 2.8125em;
  // TODO:v0.2 Testear las propiedades comentadas 
  // y evaluar si se quedan o eliminan
  // overflow: hidden;
  // position: relative;
  // text-align: center;
  // will-change: transform;
  // vertical-align: top;
  // user-select: none;
  // transform: translate3d(0, 0, 0);
  transition: all 150ms ease-out;
  white-space: nowrap;
  margin: 0;
  line-height: 1;
  place-self: flex-end;

  &:hover{
    cursor: pointer;
    background-color: ${(p: StylingProps) =>
      !p.outline ? color.primary[p.theme ? p.theme : "default"]+"E6" : color.primary[p.theme ? p.theme : "default"]+33};
  }

  &:active{
    background-color: ${(p: StylingProps) =>
      !p.outline ? color.primary[p.theme ? p.theme : "default"] : color.primary[p.theme ? p.theme : "default"]+"4D"};
  }

  &:disabled {
    background-color: ${color.grayscale.white} !important;
    color: ${color.grayscale.silver};
    border: 1px solid ${color.grayscale.iron} !important;
    box-shadow: none;
    cursor: no-drop;
  }

  svg {
    height: ${(p) => (p.size === SIZES.SMALL ? "14" : "16")}px;
    width: ${(p) => (p.size === SIZES.SMALL ? "14" : "16")}px;
    vertical-align: top;
    margin-right: ${(p) => (p.size === SIZES.SMALL ? "4" : "6")}px;
    margin-top: ${(p) => (p.size === SIZES.SMALL ? "-1" : "-2")}px;
    margin-bottom: ${(p) => (p.size === SIZES.SMALL ? "-1" : "-2")}px;
    /* Necessary for js mouse events to not glitch out when hovering on svgs */
    pointer-events: none;
  }
  span{
    margin: 0 .6rem 0 0;
  }
  ${(p) =>
    p.containsIcon &&
    `
      svg {
        display: block;
        margin: 0;
      }
      padding: ${p.size === SIZES.SMALL ? "7" : "12"}px;
    `}
`;

export interface ConfigProps {}

export const Button = forwardRef<
  unknown,
  PropsWithChildren<
    ConfigProps &
      StylingProps &
      (JSX.IntrinsicElements["button"] & JSX.IntrinsicElements["a"])
  >
>(({ children, theme = "default", ...rest }, ref) => {
    return (
      <StyledButton
        theme={theme}
        {...rest}
        ref={ref as ComponentProps<typeof StyledButton>["ref"]}
      >
        {children}
      </StyledButton>
    );
});
