import { forwardRef, ComponentProps } from "react";
import styled from "styled-components";
import { color, typography, THEMES } from "../../components/shared/styles";

export interface StylingBigButton {
  id: string;
  type?: "email" | "link";
  theme?: "jungle" | "punch" | "studio" | "havelock";
}

const WrapperBigButton = styled.div`
  display: inline-block;
`

const StyledBigButton = styled.button<StylingBigButton>`
  border-radius: 6px;
  border: 1px solid ${color.grayscale.dust};
  width: 10em;
  height: 10em;
  background: ${color.grayscale.white};
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: 350ms;

  & > [class^="icon-"] {
    font-size: 3.9em;
    color: ${color.secondary.nile};
  }

  &:focus-visible,
  &:focus,
  &:hover {
    outline: none;
    ${(props) =>
      props.theme == THEMES.JUNGLE &&
      `
      box-shadow: 0 0 2px ${color.primary.jungle};
    `}
    ${(props) =>
      props.theme == THEMES.PUNCH &&
      `
      box-shadow: 0 0 2px ${color.primary.punch};
    `}
    ${(props) =>
      props.theme == THEMES.STUDIO &&
      `
      box-shadow: 0 0 2px ${color.primary.studio};
    `}
    ${(props) =>
      props.theme == THEMES.HAVELOCK &&
      `
      box-shadow: 0 0 2px ${color.primary.havelock};
    `}
  }

  &:active {
    ${(props) =>
      props.theme == THEMES.JUNGLE &&
      `
      background-color: ${color.primary.jungle}22;
    `}
    ${(props) =>
      props.theme == THEMES.PUNCH &&
      `
      background-color: ${color.primary.punch}22;
    `}
    ${(props) =>
      props.theme == THEMES.STUDIO &&
      `
      background-color: ${color.primary.studio}22;
    `}
    ${(props) =>
      props.theme == THEMES.HAVELOCK &&
      `
      background-color: ${color.primary.havelock}22;
    `}
  }
`;
const Label = styled.span`
  color: ${color.grayscale.shark};
  font-size: .86em; /* Cambia esto para hacer una medida responsive --typography.size.m1*/;
  position: absolute;
  bottom: 10px;
`;

export const BigButton = forwardRef<unknown, StylingBigButton>(
  ({ id, theme = THEMES.JUNGLE, type = "email" }, ref) => {
    return (
      <WrapperBigButton>
        <StyledBigButton
          id={id}
          name={id}
          theme={theme}
          ref={ref as ComponentProps<typeof StyledBigButton>["ref"]}
        >
          <span className={"icon-" + type}></span>
          <Label>
            {type === "email"
              ? "Correo electrónico"
              : type === "link" && "Enlace"}
          </Label>
        </StyledBigButton>
      </WrapperBigButton>
    );
  }
);
