/**
 * Input date main controller
 * ------------------------------
 */

export interface InputDateProps {
  id: string;
  label?: string;
  inputDisplay?: "calendar" | "time" | "both";
}

export interface TimeProps {
  visible: boolean;
}

export const InputDate = forwardRef<unknown, InputDateProps>(
  ({ id, label, inputDisplay="both", ...rest }, ref) => {
    const dateRef = useRef(
      ref as ComponentProps<typeof StyledInputDate>["ref"]
    );
    const timeRef = useRef(
      ref as ComponentProps<typeof StyledInputDate>["ref"]
    );
    // Date
    const [textDate, settextDate] = useState<string>("");
    const [inputDate, setInputDate] = useState<string>("");
    // Time
    const [textTime, setTextTime] = useState<string>("");
    const [visibleTimePicker, setVisibleTimePicker] = useState<boolean>(false);
    const [hourSelected, setHourSelected] = useState<number>(-1);
    const [timeSlot, setTimeSlot] = useState<string>("");
    const ESmonths = [
      "Ene",
      "Feb",
      "Mar",
      "Abr",
      "May",
      "Jun",
      "Jul",
      "Ago",
      "Sep",
      "Oct",
      "Nov",
      "Dic",
    ];

    const hours = [
      "01:00",
      "01:30",
      "02:00",
      "02:30",
      "03:00",
      "03:30",
      "04:00",
      "04:30",
      "05:00",
      "05:30",
      "06:00",
      "06:30",
      "07:00",
      "07:30",
      "08:00",
      "08:30",
      "09:00",
      "09:30",
      "10:00",
      "10:30",
      "11:00",
      "11:30",
      "12:00",
      "12:30",
    ];

    useEffect(() => {
      document.addEventListener("mousedown", handleClickOutside);
    }, [handleClickOutside]);

    return (
      <WrapperInput {...rest}>
        {label && <Label>{label}</Label>}
        <br />
        <Toggle>
          {inputDisplay !== "time" && (
            <ToggleDate>
              <StyledInputDate
                ref={dateRef}
                id={id + "-date"}
                name={id + "-date"}
                type={"date"}
                onChange={handleChangeDate}
                value={inputDate}
              />
              <InputTarget
                readOnly
                type="text"
                value={textDate}
                placeholder="01/Oct/2021"
              />
            </ToggleDate>
          )}
          {inputDisplay !== "calendar" && (
            <ToggleDate id={id + "-time"} ref={timeRef}>
              <InputTarget
                type="text"
                readOnly
                value={textTime}
                placeholder="00:00 AM"
                onClick={toggleTimePicker}
              />
              <TimePicker visible={visibleTimePicker} onBlur={toggleTimePicker}>
                <ListHours height="179px">
                  {hours.map((hour, index) => {
                    return (
                      <StyledHour
                        key={"hour-" + index}
                        onClick={() => handleChangeTime(hour, index)}
                        className={hourSelected === index ? "selected" : ""}
                      >
                        {hour}
                      </StyledHour>
                    );
                  })}
                </ListHours>
                <ContainerTimeSlot>
                  <TimeSlot
                    onClick={handleSelectTimeSlot}
                    className={timeSlot === "AM" ? "selected" : ""}
                  >
                    AM
                  </TimeSlot>
                  <TimeSlot
                    onClick={handleSelectTimeSlot}
                    className={timeSlot === "PM" ? "selected" : ""}
                  >
                    PM
                  </TimeSlot>
                </ContainerTimeSlot>
              </TimePicker>
            </ToggleDate>
          )}
        </Toggle>
      </WrapperInput>
    );

    function handleClickOutside(event: MouseEvent) {
      if (timeRef.current && !timeRef.current.contains(event.target))
        setVisibleTimePicker(false);
    }

    function handleChangeDate(e: React.ChangeEvent<HTMLInputElement>) {
      let value = e.target.value;
      let split = value.split("-");
      let month = ESmonths[parseInt(split[1]) - 1];
      setInputDate(value);
      if (value !== "")
        // day / month / year
        settextDate(split[2] + "/" + month + "/" + split[0]);
    }

    function handleChangeTime(hour: string, index: number) {
      var timeSlotSelected = timeSlot !== "" ? timeSlot : " --";
      setHourSelected(index);
      setTextTime(hour + " " + timeSlotSelected);
      if (timeSlot !== "") toggleTimePicker();
    }

    function handleSelectTimeSlot(e: React.MouseEvent<HTMLSpanElement>) {
      var vHour = hourSelected !== -1 ? hours[hourSelected] : "--:--";
      setTimeSlot(e.currentTarget.innerHTML);
      setTextTime(vHour + " " + e.currentTarget.innerHTML);
      if (hourSelected !== -1) toggleTimePicker();
    }

    function toggleTimePicker() {
      setVisibleTimePicker(!visibleTimePicker);
    }
  }
);
