import {
  forwardRef,
  ComponentProps,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { color, typography, THEMES } from "../../components/shared/styles";

export interface DivProps {
  line?: number;
  align?: 'left' | 'center' | 'right'
}

export interface TextProps{
  color?: string;
  weight?: "light" | "regular" | "medium" | "semibold" | "bold" | "black"
}

const StyledDiv = styled.div<DivProps & { children: ReactElement }>`
  line-height: ${p=>(p.line?p.line + 'px': 'normal')};
  text-align: ${p=>(p.align?p.align : 'initial')};
`;

export const H1 = styled.h1<TextProps>`
  font-family: 'Urbanist', sans-serif;
  font-size: ${typography.size.h1}px;
  color: ${p => (p.color?p.color:color.secondary.nile)};
  font-weight: ${p => (p.weight?weightProcessor(p.weight):typography.weight.regular)};
`

