import {
  ComponentProps,
  forwardRef,
  PropsWithChildren,
  ReactElement,
} from "react";
import styled from "styled-components";
import { H1 } from "../..";
// import { color } from "../components/shared/styles";
import { color } from "../../components/shared/styles";

export interface SubheaderProps {
  title?: string;
  horizontalAlign?: "normal" | "spacing" | "center";
  verticalAlign?: "top" | "middle" | "bottom";
}

const StyledSubheader = styled.div<SubheaderProps & { children: ReactElement }>`
  display: flex;
  width: 100%;
  border-bottom: 1px solid ${color.grayscale.silver}33;
  padding: 15px 10px;
  min-height: 30.5px;
  ${(props) =>
    props.verticalAlign === "top" &&
    `
      align-items: flex-start;
    `}
  ${(props) =>
    props.verticalAlign === "middle" &&
    `
    align-items: center;
  `}
  ${(props) =>
    props.verticalAlign === "bottom" &&
    `
    align-items: flex-end;
  `}
  ${(props) =>
    props.horizontalAlign === "spacing" &&
    `
      justify-content: space-between;
    `}
  ${(props) =>
    props.horizontalAlign === "center" &&
    `
      justify-content: center;
    `}
`;

export const Subheader = forwardRef<
  unknown,
  PropsWithChildren<SubheaderProps & JSX.IntrinsicElements["div"]>
>(({ title, horizontalAlign = "spacing",verticalAlign, children, ...rest }, ref) => {
  return (
    <StyledSubheader
      {...rest}
      horizontalAlign={horizontalAlign}
      verticalAlign={verticalAlign}
      ref={ref as ComponentProps<typeof StyledSubheader>["ref"]}
    >
      <>
        {title ? <H1>{title}</H1> : null}
        {children}
      </>
    </StyledSubheader>
  );
});
