import { ComponentProps, forwardRef, useRef, useState } from "react";
import styled from "styled-components";
import { color, THEMES, typography } from "../../components/shared/styles";
import { Label } from "../../general/typography";

export interface StylingTextarea {
  id: string;
  label?: string;
  maxLength?: number;
  value?: string;
  theme?: typeof THEMES[keyof typeof THEMES];
  placeholder?: string;
}

const ContainerTextarea = styled.div`
  position: relative;
`;

const StyledTextarea = styled.textarea<StylingTextarea>`
  border: 1px solid ${color.grayscale.dust};
  border-radius: 3px;
  padding: 1.25em 1em 2.625em 1.0625em;
  font-size: 1em;
  line-height: 1.5em;
  box-sizing: border-box;
  width: 100%;
  font-family: ${typography.type.primary};
  color: ${color.grayscale.shark};

  &:focus-visible,
  &:focus {
    outline: none;
    box-shadow: 0 0 2px ${(p: StylingTextarea) => color.primary[p.theme ? p.theme : "default"]};
  }

  &::placeholder {
    color: ${color.grayscale.silver};
  }
`;

export const TextCounter = styled.p`
  text-align: right;
  color: ${color.grayscale.dust};
  font-family: ${typography.type.primary};
  margin-top: 0;
  margin-bottom: 20px;
`;

export const Textarea = forwardRef<
  unknown,
  StylingTextarea & JSX.IntrinsicElements["textarea"]
>(
  (
    { id, label, maxLength, value, placeholder, required, theme = THEMES.DEFAULT },
    ref
  ) => {
    const textareaRef = useRef(
      ref as ComponentProps<typeof StyledTextarea["ref"]>
    );
    const [characterCount, setcharacterCount] = useState(maxLength);
    return (
      <div>
        {label && <Label htmlFor={id}>{label}{required?"*":""}</Label>}
        <ContainerTextarea>
          <StyledTextarea
            id={id}
            maxLength={maxLength}
            theme={theme}
            placeholder={placeholder}
            ref={textareaRef}
            required={required}
            onChange={(e) =>
              setcharacterCount(e.target.maxLength - e.target.value.length)
            }
          >
            {value}
          </StyledTextarea>
          {characterCount && characterCount >= 0 ? (
            <TextCounter>{characterCount}</TextCounter>
          ) : characterCount === 0 ? (
            <TextCounter>0</TextCounter>
          ) : null}
        </ContainerTextarea>
      </div>
    );
  }
);
