import { forwardRef, useRef, ComponentProps } from 'react';
import styled from 'styled-components';
import { color, typography, THEMES } from '../../components/shared/styles';
import { Label } from '../../general/typography'


const SIZES = {
  SMALL: 'small',
  MEDIUM: 'medium',
  LARGE: 'large'
} as const;

const SelectWrapper = styled.span`
  position: relative;
  display:inline-block;
`

const IconSelect = styled.span<IconProps>`
  position:absolute;
  margin-left: ${p =>(p.appearance == 'large'?'-30px':'-30px')};
  color: ${color.grayscale.shark};
  top: calc(50% - 2px);
  font-size:10px;
`

const StyledSelect = styled.select<SelectProps>`
  appearance: none;
  padding: ${p=> (p.appearance == 'large' ? '18px 20px' : p.appearance == 'small' ? '5px 6px' : '10px 11px')};
  margin-bottom: 1.25em;
  border-radius: 3px;
  color: ${color.grayscale.shark};
  border: 1px solid ${color.grayscale.dust};
  font-weight: ${typography.weight.regular};
  font-family: ${typography.type.primary};
  font-size: ${p=> (p.appearance == 'large' ? typography.size.m3 : p.appearance == 'small' ? typography.size.s3 : typography.size.m1)}px;
  color: ${color.grayscale.black};
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 50px;
  width:100%;

  &:focus-visible, &:focus {
    outline: none;
    box-shadow: 0 0 2px ${(p: SelectProps) => color.primary[p.theme ? p.theme : "jungle"]};
`

export interface SelectProps{
  id: string;
  legend?: string;
  appearance?: typeof SIZES[keyof typeof SIZES];
  theme?: typeof THEMES[keyof typeof THEMES];
}

export interface IconProps{
  appearance?: typeof SIZES[keyof typeof SIZES];
}

export const Select = forwardRef<unknown, SelectProps & IconProps & (JSX.IntrinsicElements['select'])>((
  {
    id,
    children,
    appearance = SIZES.MEDIUM,
    legend,
    ...rest
  },
  ref
)=>{
  const selectRef = useRef(ref as ComponentProps<typeof StyledSelect>['ref']);
  return(
    <SelectWrapper>
    {legend&&<Label htmlFor={id}>{legend}</Label>}
    <br />
      <StyledSelect
        id={id}
        name={id}
        appearance={appearance}
        {...rest}
        ref={selectRef}
      >
        {children}
      </StyledSelect>
      <IconSelect appearance={appearance} className="icon-arrow-dropdown"></IconSelect>
    </SelectWrapper>
  )
}
)

export const Option = styled.option`
`