import styled, { css, keyframes, PickU } from "styled-components";
import { Ibar, Iitem, Ilayout } from ".";
import { ButtonIcon } from "../..";
import { color, THEMES, typography } from "../../components/shared/styles";

// **Animations
const outItem = keyframes`
  from {margin-left:0px; opacity: 1;}
  to {margin-left: 0px; opacity: 0;}
`;
const inItem = keyframes`
  from {margin-left: 0px; opacity: 0;}
  to {margin-left:0px; opacity: 1;}
`;

export const StyledLayout = styled.div<Ilayout>`
  width: 100%;
  min-height: 100%;
  background: #f4f8f9;
  ${p => p.dark?`
    #b{background-color: #2A2E3E;}
    .li-item{color: ${color.grayscale.gallery};}
  `:`
    #b{background-color: #f9fbfc;}
    .li-item{color: ${color.grayscale.storm};}
  `}
  ul {
    & > li.li-item {
      &.active{
        color: ${(p: Ilayout) => color.primary[p.theme ? p.theme : "jungle"]};
        fill: ${(p: Ilayout) => color.primary[p.theme ? p.theme : "jungle"]};
      }
      &:hover {
          color: ${(p: Ilayout) => color.primary[p.theme ? p.theme : "jungle"]};
          background-color: ${(p: Ilayout) =>
            color.primary[p.theme ? p.theme : "jungle"]}33;
        }
      }
    }
  }
`;
const SIZE_SIDEBAR = 220;
export const StyledBar = styled.div<Ibar>`
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  border-right: 1px solid #e5eaed;
  // box-shadow: 0 1px 5px #dfdfdf;
  transition: 300ms;
  padding: 0.8rem;
  ${(p) => `width: ${p.isOpen ? SIZE_SIDEBAR + "px" : "55px"};`}
  
  .li-item .item-text{
    animation-duration: 0.3s;
    animation-name: ${(p) =>
      !p.isOpen
        ? css`
            ${outItem};
            opacity: 0;
          `
        : css`
            ${inItem};
            opacity: 1;
          `};
  }

  #btnOpenSidebar{
    ${(p) => `margin-left: ${p.isOpen ? `calc(${SIZE_SIDEBAR}px - 1.85rem)` : "calc(55px - 1.85rem)"};`}
    ${(p) => `transform: ${p.isOpen ? `rotate(0deg)` : "rotate(180deg)"};`}
  }
`;

export const StyledBarMenu = styled.ul`
  list-style: none;
  padding: 0;
  width: 100%;
  // margin-left: 0.2rem;

  a {
    text-decoration: none;
    color: var(--main-color-gray);
  }
`;

export const StyledBarItem = styled.li<Iitem>`
  display: flex;
  align-items: center;
  word-break: keep-all;
  padding: .4rem;
  margin: 3px 0;
  white-space: nowrap;
  position: relative;
  transition: 0.2s;
  border-radius: 3px;
  font-weight: ${typography.weight.medium};

  &:hover {
    cursor: pointer;
  }

  .item-icon{
    margin-right: 15px;
  }

  .item-text{
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 5px;
    margin: auto 0;
  }
`;

export const SidebarTrigger = styled(ButtonIcon)`
  position: fixed;
  z-index: 1;
  top: 86vh;
  transition: 300ms;
  width: 28px;
  height: 28px;
  font-size: 18px;
  padding-top: 3px;
  padding-left: 4px;
`;

export const StyledBarFooter = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.8rem;

  & > li:hover{
    color: ${color.grayscale.black};
    text-shadow: 0 0 1px ${color.grayscale.iron};
  }
`;

export const StyledContentOut = styled.div<Ibar>`
  background-color: #F4F8F9;
  position:absolute;
  left: var(--leftSidebarWidth);
  width: calc(100vw - var(--leftSidebarWidth,0px));
  min-height: 100%;
  transition: 300ms;
  overflow: hidden;
`